@media (max-width: 480px) {
    .beerAll {
        position: relative;
        display: flex;
        flex-wrap: nowrap;
        width: 100%;
        height: 100%;
    }
    .beerAll.blurred {
        filter: blur(10px);
        pointer-events: none;
        transition: filter 0.3s ease-in-out;
    }
    .beerContainer {
        display: flex;
        flex-wrap: nowrap;
        transition: transform 0.3s ease-in-out;
        height: 100%;
    }

    .beerList {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
        background-color: white;
        position: relative;
        left: -2px;
        top: 30px;
        gap: 4px;
        height: 100%;
        transition: opacity 0.3s ease-in-out;
    }

    .beerItem {
        width: calc(40% - 50px);
        height: 125px;
        box-sizing: border-box;
        margin: 5px 5px 15px 5px;
    }

    .imgBeerAll {
        border: 1px solid black;
        border-radius: 8px;
        width: 63%;
        height: 90px;
        object-fit: cover;
    }

    .nameBeerh3 {
        font-size: 0.8rem;
        text-align: center;
        margin-top: -5px
    }

    .priceBeerP {
        text-align: center;
        font-size: 0.6rem;
        margin-top: -10px;
    }

    #pLoading {
        position: relative;
        left: 45%;
        text-align: center;
    }
}

