@media (max-width: 480px) {
    #btnClose {
        background-color: red;
        border-radius: 10px;
        padding: 2px;
        height: 3.5%;
        width: 13%;
        position: absolute;
        top: 15%;
        left: 6%;
        z-index: 102;
        font-size: 11px;
    }

    .beerSee {
        position: fixed;
        top: 19.5%;
        left: 6%;
        display: flex;
        flex-direction: column;
        z-index: 100;
        width: 88vw;
        border: 1px solid black;
        border-radius: 13px;
        background-color: #282c34;
        gap: 2px;
        box-shadow: 10px 12px 10px #282c34;
        height: auto;
        max-height: 42vh;
    }

    .beerListSee {
        position: relative;
        top: -15px;
        left: 18px;
        display: flex;
        flex-direction: column;
        z-index: 110;
        height: auto;
        max-height: 42vh
    }

    .beerItemSee {
        color: white;
        text-align: left;
        width: 100%;
    }

    .imgBeerSee {
        width: 24%;
        height: auto;
        position: relative;
        z-index: 110;
        left: 59%;
        top: 15vh;
        margin-left: 5px;
        margin-bottom: 5px;
        border-radius: 13px;
    }

    .nameBeerh3See {
        position: relative;
        font-size: 1.2rem;
        letter-spacing: 1px;
        top: -65px;
    }

    .tagLineSee {
        position: relative;
        width: 60%;
        font-size: 0.9em;
        letter-spacing: 1px;
        top: -79px;
    }

    .abvSee {
        position: relative;
        font-size: 0.9em;
        letter-spacing: 1px;
        top: -90px;
    }

    .descriptionContainer {
        position: relative;
        letter-spacing: 1px;
        font-size: 0.9em;
        line-height: 110%;
        width: 59%;
        top: -100px;
        z-index: 110;
    }

    .food_pairingSeeContainer {
        position: relative;
        letter-spacing: 1px;
        font-size: 0.9em;
        line-height: 110%;
        width: 59%;
        top: -113px;
        z-index: 110;
    }

    .collapseButtonD,  .collapseButtonP {
        position: relative;
        width: 46%;
        font-size: 1.1em;
        top: -8px;
        border-radius: 5px;
        border: none;
    }

    #btnAddCart {
        position: absolute;
        top: 38vh;
        right: 8%;
        width: 23vh;
        height: 30px;
        font-size: 1rem;
        font-weight: bold;
        border-radius: 12px;
    }
}

